'use client';

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

type Language = 'en' | 'ar';

const translations = {
    en: {
        settings: "Settings",
        manageAccount: "Manage your account settings and preferences.",
        profile: "Profile",
        updateProfile: "Update your personal information.",
        changeAvatar: "Change Avatar",
        uploadAvatar: "Upload a new profile picture.",
        name: "Name",
        email: "Email",
        password: "Password",
        changePassword: "Change your password.",
        currentPassword: "Current Password",
        newPassword: "New Password",
        saveChanges: "Save Changes",
        language: "Language",
        selectLanguage: "Select your preferred language.",
    },
    ar: {
        settings: "الإعدادات",
        manageAccount: "إدارة إعدادات حسابك وتفضيلاتك.",
        profile: "الملف الشخصي",
        updateProfile: "تحديث معلوماتك الشخصية.",
        changeAvatar: "تغيير الصورة الرمزية",
        uploadAvatar: "تحميل صورة ملف شخصي جديدة.",
        name: "الاسم",
        email: "البريد الإلكتروني",
        password: "كلمة المرور",
        changePassword: "تغيير كلمة المرور الخاصة بك.",
        currentPassword: "كلمة المرور الحالية",
        newPassword: "كلمة المرور الجديدة",
        saveChanges: "حفظ التغييرات",
        language: "اللغة",
        selectLanguage: "اختر لغتك المفضلة.",
    }
};

export default function SettingsPage() {
    const [avatarPreview, setAvatarPreview] = useState<string | null>(null);
    const [language, setLanguage] = useState<Language>('en');

    const handleAvatarChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            const file = e.target.files[0];
            const reader = new FileReader();
            reader.onloadend = () => {
                setAvatarPreview(reader.result as string);
            };
            reader.readAsDataURL(file);
        }
    };
    
    const t = translations[language];

    return (
        <div className="space-y-6" dir={language === 'ar' ? 'rtl' : 'ltr'}>
            <div>
                <h1 className="text-3xl font-bold font-headline">{t.settings}</h1>
                <p className="text-muted-foreground">{t.manageAccount}</p>
            </div>

            <Card>
                <CardHeader>
                    <CardTitle>{t.language}</CardTitle>
                    <CardDescription>{t.selectLanguage}</CardDescription>
                </CardHeader>
                <CardContent>
                    <Select value={language} onValueChange={(value) => setLanguage(value as Language)}>
                        <SelectTrigger className="w-[180px]">
                            <SelectValue placeholder="Select language" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="en">English</SelectItem>
                            <SelectItem value="ar">العربية</SelectItem>
                        </SelectContent>
                    </Select>
                </CardContent>
            </Card>
            
            <Card>
                <CardHeader>
                    <CardTitle>{t.profile}</CardTitle>
                    <CardDescription>{t.updateProfile}</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                    <div className="flex items-center space-x-4">
                        <Avatar className="h-20 w-20">
                          <AvatarImage src={avatarPreview || "https://placehold.co/96x96.png"} alt="User" data-ai-hint="man suit" />
                          <AvatarFallback>AD</AvatarFallback>
                        </Avatar>
                        <div className="space-y-2 flex-1">
                            <Label htmlFor="avatar-upload">{t.changeAvatar}</Label>
                            <Input id="avatar-upload" type="file" accept="image/*" onChange={handleAvatarChange} />
                            <p className="text-xs text-muted-foreground">
                                {t.uploadAvatar}
                            </p>
                        </div>
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="name">{t.name}</Label>
                        <Input id="name" defaultValue="Admin" />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="email">{t.email}</Label>
                        <Input id="email" type="email" defaultValue="admin@telepowerx.com" />
                    </div>
                </CardContent>
            </Card>

            <Card>
                <CardHeader>
                    <CardTitle>{t.password}</CardTitle>
                    <CardDescription>{t.changePassword}</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                    <div className="space-y-2">
                        <Label htmlFor="current-password">{t.currentPassword}</Label>
                        <Input id="current-password" type="password" />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="new-password">{t.newPassword}</Label>
                        <Input id="new-password" type="password" />
                    </div>
                </CardContent>
            </Card>
            
            <div className="flex justify-end">
                <Button>{t.saveChanges}</Button>
            </div>
        </div>
    )
}
